<template>
  <el-dialog
    custom-class="login-dialog"
    title="회원가입"
    v-model="state.dialogVisible"
    @close="handleClose"
  >
    <template #header>
      <span class="el-dialog__headerbtn">
        <el-button @click="handleClose"
          ><el-button type="primary" icon="el-icon-close" circle></el-button
        ></el-button>
      </span>
    </template>

    <el-form
      :model="state.form"
      :rules="state.rules"
      ref="regForm"
      :label-position="state.form.align"
    >
      <el-form-item
        prop="department"
        label="소속"
        :label-width="state.formLabelWidth"
      >
        <el-input v-model="state.form.department" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item
        prop="position"
        label="직책"
        :label-width="state.formLabelWidth"
      >
        <el-input v-model="state.form.position" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item
        prop="name"
        label="이름"
        :label-width="state.formLabelWidth"
      >
        <el-input v-model="state.form.name" autocomplete="off"></el-input>
      </el-form-item>

      <el-form-item
        prop="id"
        label="아이디"
        :label-width="state.formLabelWidth"
      >
        <el-input v-model="state.form.id" autocomplete="off"></el-input>
        <el-button @click="checkId">중복 확인</el-button>
      </el-form-item>

      <el-form-item
        prop="password"
        label="비밀번호"
        :label-width="state.formLabelWidth"
      >
        <el-input
          v-model="state.form.password"
          autocomplete="off"
          show-password
        ></el-input>
      </el-form-item>
      <el-form-item
        prop="passwordcheck"
        label="비밀번호확인"
        :label-width="state.formLabelWidth"
      >
        <el-input
          v-model="state.form.passwordcheck"
          autocomplete="off"
          show-password
        ></el-input>
      </el-form-item>
    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button type="primary" plain :disabled="!state.form.passwordcheck || !state.form.name ||!state.form.id || !state.form.password" @click="clickReg">회원가입</el-button>
      </span>
    </template>
  </el-dialog>
</template>
<style>
.login-dialog {
  width: 400px !important;
  height: 600px;
}
.login-dialog .el-dialog__headerbtn {
  float: right;
}
.login-dialog .el-form-item__content {
  margin-left: 0 !important;
  float: right;
  width: 200px;
  display: inline-block;
}
.login-dialog .el-form-item {
  margin-bottom: 20px;
}
.login-dialog .el-form-item__error {
  font-size: 12px;
  color: red;
}
.login-dialog .el-input__suffix {
  display: none;
}
.login-dialog .el-dialog__footer {
  margin: 0 calc(50% - 80px);
  padding-top: 0;
  display: inline-block;
}
.login-dialog .dialog-footer .el-button {
  width: 120px;
}
</style>
<script>
import { reactive, computed, ref, onMounted } from "vue";
import { useStore } from "vuex";

export default {
  name: "reg-dialog",

  props: {
    open: {
      type: Boolean,
      default: false
    }
  },

  setup(props, { emit }) {
    const store = useStore();
    // 마운드 이후 바인딩 될 예정 - 컨텍스트에 노출시켜야함. <return>
    const regForm = ref(null);

    /*
      // Element UI Validator
      // rules의 객체 키 값과 form의 객체 키 값이 같아야 매칭되어 적용됨
      //
    */



   const validateID = (rule, value, callback) => {
      if (!state.form.checkId) {
        callback(new Error("아이디 중복확인을 해주세요."))
      } else {
        callback()
      }
    }
  const validatePassword = (rule, value, callback) => {
      if (value !== state.form.password) {
        callback(new Error("입력한 비밀번호와 일치하지 않습니다."))
      } else {
        callback()
      }
    }

    const state = reactive({
      form: {
        department: "",
        position: "",
        name: "",
        id: "",
        checkId: false,
        password: "",
        passwordcheck: "",
        align: "left"
      },
      rules: {
        department: [
          { max: 30, message: "최대 30자까지 입력 가능합니다", trigger: "blur" }
        ],
        position: [
          { max: 30, message: "최대 30자까지 입력 가능합니다", trigger: "blur" }
        ],
        name: [
          { required: true, message: "필수 입력 항목입니다", trigger: "blur" },
          { max: 30, message: "최대 30자까지 입력 가능합니다", trigger: "blur" }
        ],

        id: [
          { required: true, message: "필수 입력 항목입니다", trigger: "blur" },
          // 유효성 체크 start
          { max: 16, message: "최대 16자까지 입력 가능합니다", trigger: "blur" },
          { validator: validateID, trigger: 'blur' }
          // end
        ],
        password: [
          { required: true, message: "필수 입력 항목입니다", trigger: "blur" },
          {
            max: 16,
            message: "최대 16자까지 입력 가능합니다",
            trigger: "blur"
          },
          // 유효성 체크 start
          {
            min: 9,

            message: "최소 9글자를 입력해야 합니다",
            trigger: "blur"
          },
          {
            pattern:
              "^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+])(?!.*[^a-zA-z0-9$`~!@$!%*#^?&\\(\\)\-_=+]).{3,}$",
            message: "비밀번호는 영문, 숫자, 특수문자가 조합되어야 합니다."
          }
          // end
        ],
        passwordcheck: [
          { required: true, message: "필수 입력 항목입니다", trigger: "blur" },
          {
            max: 16,
            message: "최대 16자까지 입력 가능합니다",
            trigger: "blur"
          },
          // 유효성 체크 start
          {
            min: 9,

            message: "최소 9글자를 입력해야 합니다",
            trigger: "blur"
          },
          {
            pattern:
              "^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+])(?!.*[^a-zA-z0-9$`~!@$!%*#^?&\\(\\)\-_=+]).{3,}$",
            message: "비밀번호는 영문, 숫자, 특수문자가 조합되어야 합니다."
          },
          {
            validator: validatePassword, trigger: 'blur'
          }
          // end
        ]
      },
      dialogVisible: computed(() => props.open),
      formLabelWidth: "120px"
    });

    onMounted(() => {
      // console.log(loginForm.value)
    });

    const checkId = function() {
      console.log("checkId Submit");
      store
        .dispatch("root/requestCheckId", state.form.id)
        .then(function(result) {
          alert("사용할 수 있는 id입니다.");
          state.form.checkId = true;
        })
        .catch(function(err) {
          alert('이미 존재하는 id 입니다.')
        });
    };

    const clickReg = function() {
      // 로그인 클릭 시 validate 체크 후 그 결과 값에 따라, 로그인 API 호출 또는 경고창 표시
      regForm.value.validate(valid => {
        if (valid) {
          console.log("submit");
          store
            .dispatch("root/requestReg", {
              userId: state.form.id,
              password: state.form.password,
              department: state.form.department,
              position: state.form.position,
              name: state.form.name
            })
            .then(function(result) {
              //alert("accessToken: " + result.data.accessToken);
              alert("회원가입이 완료되었습니다")
              handleClose();
              //loading = true;
            })
            .catch(function(err) {
              alert("회원가입이 실패하였습니다");
            });
        } else {
          alert("입력사항을 확인해주세요");
        }
      });
    };

    const handleClose = function() {
      state.form.department = "";
      state.form.position = "";
      state.form.name = "";
      state.form.id = "";
      state.form.password = "";
      state.form.passwordcheck = "";

      emit("closeRegDialog");
    };

    return { regForm, state, clickReg, handleClose, checkId };
  }
};
</script>
