<template>
  <el-dialog

    custom-class="login-dialog"
    title="로그인"
    v-model="state.dialogVisible"
    @close="handleClose"
  >
    <!-- <el-button class="el-dialog__headerbtn">X</el-button> -->

    <template #header>
      <span class="el-dialog__headerbtn">
        <el-button @click="handleClose"
          ><el-button type="primary" icon="el-icon-close" circle></el-button
        ></el-button>
      </span>
    </template>

    <el-form
      v-loading="state.loading"
      :model="state.form"
      :rules="state.rules"
      ref="loginForm"
      :label-position="state.form.align"
    >
      <el-form-item
        prop="id"
        label="아이디"
        :label-width="state.formLabelWidth"
      >
        <el-input v-model="state.form.id" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item
        prop="password"
        label="비밀번호"
        :label-width="state.formLabelWidth"
      >
        <el-input
          v-model="state.form.password"
          autocomplete="off"
          show-password
        ></el-input>
      </el-form-item>
    </el-form>

    <template #footer>
      <span class="dialog-footer">
        <el-button :disabled="!state.form.id || !state.form.password" @click="clickLogin">로그인</el-button>
      </span>
    </template>
  </el-dialog>
</template>
<style>
.login-dialog {
  width: 400px !important;
  height: 300px;
}
.login-dialog .el-dialog__headerbtn {
  float: right;
}
.login-dialog .el-form-item__content {
  margin-left: 0 !important;
  float: right;
  width: 200px;
  display: inline-block;
}
.login-dialog .el-form-item {
  margin-bottom: 20px;
}
.login-dialog .el-form-item__error {
  font-size: 12px;
  color: red;
}
.login-dialog .el-input__suffix {
  display: none;
}
.login-dialog .el-dialog__footer {
  margin: 0 calc(50% - 80px);
  padding-top: 0;
  display: inline-block;
}
.login-dialog .dialog-footer .el-button {
  width: 120px;
}
</style>
<script>
import { reactive, computed, ref, onMounted } from "vue";
import { useStore } from "vuex";
import { useRouter } from "vue-router";
export default {
  name: "login-dialog",

  props: {
    open: {
      type: Boolean,
      default: false
    }
  },

  setup(props, { emit }) {
    const router = useRouter();
    const store = useStore();
    // 마운드 이후 바인딩 될 예정 - 컨텍스트에 노출시켜야함. <return>
    const loginForm = ref(null);

    /*
      // Element UI Validator
      // rules의 객체 키 값과 form의 객체 키 값이 같아야 매칭되어 적용됨
      //
    */

    const state = reactive({
      form: {
        id: "",
        password: "",
        align: "left",
      },
      loading : false,
      rules: {
        id: [
          { required: true, message: "필수 입력 항목입니다", trigger: "blur" },
          // 유효성 체크 start
          { max: 16, message: "최대 16자까지 입력 가능합니다", trigger: "blur" },

          // end
        ],
        password: [
          { required: true, message: "필수 입력 항목입니다", trigger: "blur" },
          {
            max: 16,
            message: "최대 16자까지 입력 가능합니다",
            trigger: "blur"
          },
          // 유효성 체크 start
          {
            min: 9,

            message: "최소 9글자를 입력해야 합니다",
            trigger: "blur"
          }
          // ,{
          //   pattern:
          //     "^(?=.*[a-zA-z])(?=.*[0-9])(?=.*[$`~!@$!%*#^?&\\(\\)\-_=+])(?!.*[^a-zA-z0-9$`~!@$!%*#^?&\\(\\)\-_=+]).{3,}$",
          //   message: "비밀번호는 영문, 숫자, 특수문자가 조합되어야 합니다."
          // }
          // end
        ],
      },
      dialogVisible: computed(() => props.open),
      formLabelWidth: "120px"
    });

    onMounted(() => {
      // console.log(loginForm.value)
    });

    const clickLogin = function() {
      // 로그인 클릭 시 validate 체크 후 그 결과 값에 따라, 로그인 API 호출 또는 경고창 표시
      //loading()
      state.loading = true

      loginForm.value.validate(valid => {
        if (valid) {
          console.log("submit");
          store
            .dispatch("root/requestLogin", {
              id: state.form.id,
              password: state.form.password
            })
            .then(function(result) {
              //alert("accessToken: " + result.data.accessToken);
              // 명세_인증/토큰관리
              // 1. 로그인 및 토큰 발급 시, 해당 토큰을 localStorage에 저장하여 관리
              localStorage.setItem(
                "accessToken",
                "Bearer " + result.data.accessToken
              );
              state.loading = false;
              emit("closeLoginDialog")
              handleClose();




              router.go()
              alert("로그인 되었습니다")

            })
            .catch(function(err) {
              console.log(err)
              state.loading = false;
              alert("로그인 실패!");

            });
        } else {
          state.loading = false;
          alert("Validate error!");
        }
      });
    };

    const handleClose = function() {
      state.form.id = "";
      state.form.password = "";
      emit("closeLoginDialog")
      // if(state.loading){
      //   state.loading = false;
      //   emit("fullscreenLoading")
      // }
      // else emit("closeLoginDialog");

    };
    const loading = function(){
      console.log("log-diaglogue loading")
      emit("fullscreenLoading");
    };

    return { loginForm, state, clickLogin, handleClose,loading };
  }
};
</script>
