<template>
  <el-container class="main-wrapper">
    <main-header
      :height="`70px`"
      @openLoginDialog="onOpenLoginDialog"
      @openRegDialog="onOpenRegDialog"
      @fullscreenLoading="openFullScreen1"
    />
    <el-container class="main-container">
      <el-aside class="hide-on-small" width="240px">
        <main-sidebar :width="`240px`" />
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>

    <main-footer :height="`110px`" />
  </el-container>
  <login-dialog
    :open="loginDialogOpen"
    @fullscreenLoading="openFullScreen1"
    @closeLoginDialog="onCloseLoginDialog"
  />
  <reg-dialog :open="regDialogOpen" @closeReginDialog="onCloseRegDialog" />

  <el-button
    type="primary"
    @click="openFullScreen1"
    v-loading.fullscreen.lock="fullscreenLoading"
  >
    As a directive
  </el-button>
</template>
<style>
@import "https://unpkg.com/element-plus/theme-chalk/index.css";
@import "./main.css";
@import "../../common/css/common.css";
@import "../../common/css/element-plus.css";
</style>
<script>
import LoginDialog from "./components/login-dialog";
import RegDialog from "./components/reg-dialog";
import MainHeader from "./components/main-header";
import MainSidebar from "./components/main-sidebar";
import MainFooter from "./components/main-footer";
import { useStore } from 'vuex'
export default {
  name: "Main",
  components: {
    MainHeader,
    MainSidebar,
    MainFooter,
    LoginDialog,
    RegDialog
  },
  data() {
    return {
      loginDialogOpen: false,
      regDialogOpen: false,
      fullscreenLoading: false
    };
  },
  methods: {
    openFullScreen1() {
        this.fullscreenLoading = true;
        setTimeout(() => {
          this.fullscreenLoading = false;
        }, 2000);
    },
    onOpenLoginDialog() {
      this.loginDialogOpen = true;
    },
    onCloseLoginDialog() {
      this.loginDialogOpen = false;
    },
    onOpenRegDialog() {
      this.regDialogOpen = true;
    },
    onCloseRegDialog() {
      this.regDialogOpen = false;
    }
  },
  created() {
    const store = useStore()
    store.dispatch('root/requestAuthCheck',localStorage.getItem('accessToken'))
          .then(function (result) {
            console.log("result ", result)
            if(result){
              store.commit('root/setLogin')
              console.log('토큰이 유효합니다'+store.getters['root/getIsLogin'])

            }else{
              store.commit('root/setLogout')
              console.log("토큰 무유효")
            }
            //store.commit('root/setLogout')

          }).then(function() {
        if (store.getters["root/getIsLogin"]) {
          store.dispatch("root/requestMe",localStorage.getItem('accessToken')).then(function(result) {
            console.log("result : ",result.data);
            store.commit("root/setMe", result.data);
            //console.log("meapi확인 : " ,store.getters['root/getMe']);
          });
        }
      })
          .catch(function (err) {

            alert('토큰이 없는 상태입니다.'+store.getters['root/getIsLogin'])
          })
  }


};
</script>
